// –ß—Ç–æ–±—ã –Ω–µ –¥–µ–ª–∞—Ç—å –∫—É—á—É —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∞–π–¥–∏, –º–æ–∂–Ω–æ –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω—ã–º –ø–æ–ª—è–º —Ñ–æ—Ä–º—ã –¥–∞–≤–∞—Ç—å –æ–¥–∏–Ω –∫–ª–∞—Å—Å –∏ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞—Ç—å –∏—Ö –≤ —Ü–∏–∫–ª–µ
