////// –ö–û–ù–ï–¶ –î–ï–ú–û–ù–°–¢–†–ê–¶–ò–û–ù–ù–û–ì–û –£–ß–ê–°–¢–ö–ê –ö–û–î–ê. –î–∞–ª—å—à–µ –∫–æ–¥ –¥–ª—è –æ—Ü–µ–Ω–∏–≤–∞–µ–º–æ–π —á–∞—Å—Ç–∏ –∑–∞–¥–∞–Ω–∏—è


// –í—ã–ø–∏—Å—ã–≤–∞–µ–º –≤—Å–µ –∞–π–¥–∏—à–Ω–∏–∫–∏ HTMl-—ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ –∫–æ–Ω—Å—Ç–∞–Ω—Ç—ã –¥–ª—è –ø–µ—Ä–µ–∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è
const first_name_id = 'first_name'
const last_name_id = 'last_name'
const password_id = 'password'
const email_id = 'email'

const sign_in_link_id = 'sign_in_link'
const sign_up_form_id = 'sign_up_form'
// const sign_in_form_id = 'sign_in_form'  // –ü—Ä–∏–≥–æ–¥–∏—Ç—Å—è
const sign_up_btn_id = 'sign_up_btn'
const sign_in_form_id = 'sign_in_form'


// –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç DOM-–¥–µ—Ä–µ–≤–∞ –ø–æ id –∏ –ø—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∞—Ç—Ç—Ä–∏–±—É—Ç—É oninput
// oninput –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º "event" –∫–∞–∂–¥—ã–π —Ä–∞–∑, –∫–æ–≥–¥–∞ –≤–≤–æ–¥ –º–µ–Ω—è–µ—Ç—Å—è
// –ó–Ω–∞—á–µ–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ –º—ã –ø—Ä–∏—Å–≤–∞–µ–≤–∞–µ–º —ç—Ç–æ–º—É –∞—Ç—Ç—Ä–∏–±—É—Ç—É - —ç—Ç–æ —Ñ—É–Ω–∫—Ü–∏—è, –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω–∞—è –≤ —Å—Ç—Ä–µ–ª–æ—á–Ω–æ–º —Å—Ç–∏–ª–µ
// –ì—É–≥–ª–∏—Ç—å –ø–æ —Ç–µ–≥–∞–º "—Å–æ–±—ã—Ç–∏—è JS", "onchange/oninput HTML", "—Å—Ç—Ä–µ–ª–æ—á–Ω—ã–µ —Ñ—É–Ω–∫—Ü–∏–∏ JS", ...

const first_name = document.getElementById(first_name_id);
first_name.oninput = (e) => setFormValue(first_name_id, e.target.value)  // –£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –±–µ–∑ –≤–∞–ª–∏–¥–∞—Ü–∏–∏

const email = document.getElementById(email_id);
email.oninput = (e) => setFormValue(email_id, e.target.value, validateEmail) // –£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ —Å –≤–∞–ª–∏–¥–∞—Ü–∏–µ–π

const passwordField = document.getElementById(password_id);

const passwordRepeatField = document.getElementById("password-repeat");

const login_email = document.getElementById("login_email");

const login_password = document.getElementById("login_password");

const login_btn = document.getElementById("login_btn");
login_btn.onclick = (e) => {
  e.preventDefault(); // –û—Ç–∫–ª—é—á–∏—Ç—å –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã

  // –ü–æ–ª—É—á–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏—è –ø–æ–ª–µ–π
  const emailValue = document.getElementById("login_email").value.trim();
  const passwordValue = document.getElementById("login_password").value.trim();

  // –ü—Ä–æ—Å—Ç–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞: email –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –≤–∞–ª–∏–¥–Ω—ã–º, –ø–∞—Ä–æ–ª—å –Ω–µ –ø—É—Å—Ç—ã–º
  const emailValid = validateEmail(emailValue);
  const passwordValid = passwordValue !== ""; // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—É—Å—Ç–æ—Ç—É

  if (emailValid && passwordValid) {
    console.log("Login successful!");
    console.log({ email: emailValue, password: passwordValue });
  } else {
    console.log("Invalid login credentials");

    // –ü–æ–¥—Å–≤–µ—á–∏–≤–∞–µ–º –ø–æ–ª—è, –µ—Å–ª–∏ –æ–Ω–∏ –Ω–µ–≤–∞–ª–∏–¥–Ω—ã
    if (!emailValid) {
      document.getElementById("login_email").classList.add("invalid");
    }
    if (!passwordValid) {
      document.getElementById("login_password").classList.add("invalid");
    }
  }
};


// –ú–µ–Ω—è–µ–º —Å—Ç–∏–ª–∏ –æ–±—ä–µ–∫—Ç–∞ DOM –¥–µ—Ä–µ–≤–∞. –≠—Ç–æ –ø–æ–∑–≤–æ–ª—è–µ—Ç —Å–∫—Ä—ã—Ç—å —Ñ–æ—Ä–º—É —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –∏ –ø–æ–∫–∞–∑–∞—Ç—å —Ñ–æ—Ä–º—É –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
// –û–±—ä–µ–∫—Ç —Ñ–æ—Ä–º—ã –Ω–µ –∏—Å–∫–ª—é—á–∞–µ—Ç—Å—è –∏–∑ DOM –¥–µ—Ä–µ–≤–∞, –∞ –ø—Ä–æ—Å—Ç–æ —Å—Ç–∞–Ω–æ–≤–∏—Å—Ç—è –Ω–µ–≤–∏–¥–∏–º—ã–º
const switch_to_sign_in = document.getElementById(sign_in_link_id);
switch_to_sign_in.onclick = (e) => {
  document.getElementById(sign_up_form_id).style.display = "none";
  document.getElementById(sign_in_form_id).style.display = "";
  toggleLoginButton(); // –û–±–Ω–æ–≤–∏—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
};

switch_to_sign_up.onclick = (e) => {
  document.getElementById(sign_in_form_id).style.display = "none";
  document.getElementById(sign_up_form_id).style.display = "";
  toggleSignUpButton(); // –û–±–Ω–æ–≤–∏—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
};

const sign_up_btn = document.getElementById(sign_up_btn_id);
sign_up_btn.onclick = (e) => {
  // –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –∫–Ω–æ–ø–∫–∏ –≤ —Ñ–æ—Ä–º–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã.
  // –ß—Ç–æ–±—ã –æ—Ç–∫–ª—é—á–∏—Ç—å –µ–≥–æ, –Ω—É–∂–Ω–æ –æ—Ç–º–µ–Ω–∏—Ç—å —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–µ –ø–æ–≤–µ–¥–µ–Ω–∏–µ —Å–æ–±—ã—Ç–∏—è
  e.preventDefault()
  submitSignUpForm()
}

// –§—É–Ω–∫—Ü–∏—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏/—Ä–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ –∫–Ω–æ–ø–∫–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
const toggleSignUpButton = () => {
  const signUpButton = document.getElementById(sign_up_btn_id); // –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ —ç–ª–µ–º–µ–Ω—Ç –∫–Ω–æ–ø–∫–∏ –ø–æ–ª—É—á–µ–Ω
  const isValid = getValidationStatus();
  signUpButton.disabled = !isValid;
};

// –§—É–Ω–∫—Ü–∏—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏/—Ä–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ –∫–Ω–æ–ø–∫–∏ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
const toggleLoginButton = () => {
  const loginButton = document.getElementById("login_btn");
  const emailValid = validateEmail(document.getElementById("login_email").value);
  const passwordValid = document.getElementById("login_password").value.trim() !== "";
  loginButton.disabled = !(emailValid && passwordValid); // –ë–ª–æ–∫–∏—Ä—É–µ–º, –µ—Å–ª–∏ email –∏–ª–∏ –ø–∞—Ä–æ–ª—å –Ω–µ–≤–∞–ª–∏–¥–Ω—ã
};

// –í–∞–ª–∏–¥–∞—Ü–∏—è –∏–º–µ–Ω–∏ (—Ç–æ–ª—å–∫–æ –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏)
first_name.oninput = (e) => {
  setFormValue(first_name_id, e.target.value);
  toggleSignUpButton();
};

// –í–∞–ª–∏–¥–∞—Ü–∏—è email –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
email.oninput = (e) => {
  setFormValue(email_id, e.target.value, validateEmail);
  toggleSignUpButton();
};

// –í–∞–ª–∏–¥–∞—Ü–∏—è –ø–∞—Ä–æ–ª—è –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
passwordField.oninput = (e) => {
  const isValid = validatePassword(e.target.value);
  
  // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–∞—Ç—É—Å –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –¥–ª—è –ø–∞—Ä–æ–ª—è
  formValidation.password = isValid;

  if (isValid) {
    passwordField.classList.remove("invalid");
    passwordField.classList.add("valid");
  } else {
    passwordField.classList.remove("valid");
    passwordField.classList.add("invalid");
  }

  setFormValue(password_id, e.target.value, validatePassword);
  toggleSignUpButton();
};

passwordRepeatField.oninput = (e) => {
  const repeatPassword = e.target.value;
  const originalPassword = passwordField.value;

  // –ò—Å–ø–æ–ª—å–∑—É–µ–º setFormValue –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∑–Ω–∞—á–µ–Ω–∏—è –∏ —Å—Ç–∞—Ç—É—Å–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏
  setFormValue("password_repeat", repeatPassword, (value) => validatePasswordRepeat(value, originalPassword));

  // –°–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü–∏—è —Ü–≤–µ—Ç–∞ –ø–æ–ª–µ–π
  const isValid = formValidation.password_repeat; // –°—Ç–∞—Ç—É—Å –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –∏–∑ formValidation
  if (isValid) {
    passwordRepeatField.classList.remove("invalid");
    passwordRepeatField.classList.add("valid");
  } else {
    passwordRepeatField.classList.remove("valid");
    passwordRepeatField.classList.add("invalid");
  }

  // –ü–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
  toggleSignUpButton();
};

// –í–∞–ª–∏–¥–∞—Ü–∏—è email –¥–ª—è –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
login_email.oninput = (e) => {
  setFormValue("login_email", e.target.value, validateEmail);
  toggleLoginButton();
};

// –í–∞–ª–∏–¥–∞—Ü–∏—è –ø–∞—Ä–æ–ª—è –¥–ª—è –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
login_password.oninput = (e) => {
  const isValid = e.target.value.trim() !== "";
  if (isValid) {
    login_password.classList.remove("invalid");
    login_password.classList.add("valid");
  } else {
    login_password.classList.remove("valid");
    login_password.classList.add("invalid");
  }
  toggleLoginButton();
};

document.addEventListener("DOMContentLoaded", () => {
  toggleSignUpButton();
  toggleLoginButton();
});
